* {
    margin: 0;
    padding: 0;
}

.container {
    width: 100%;
    height: 100vh;
    background-image: url(https://static.vecteezy.com/system/resources/previews/014/661/403/non_2x/game-level-background-magic-road-and-fantasy-landscape-in-cartoon-style-road-green-field-and-forest-illustration-vector.jpg);
    background-repeat: no-repeat;
    background-position: center;
    background-size: auto;

    .header {
        display: flex;
        justify-content: space-around;
        align-items: center;

        h1 {
            font-size: 36px;
        }


        img {
            position: relative;
            width: 100px;
            height: 100px;
        }
    }

    .main{
        display: flex;
        align-items: center;
        justify-content: center;
        margin-top: 30px;
        img{
            height: 350px;
            width: 400px;
        }
    }
}